import React from 'react';
import styled from 'styled-components';
import { useApp } from '../../context/Context';
import textContext from '../../textContext';
import heroBG from '../../assets/videos/heroBG.mp4'
import poster from '../../assets/videos/poster.webp'

const Background = styled('div')`
	width: 100%;
	position: relative;
	z-index: 1;
	.videoContainer {
		position: relative;
		height: 100%;
		width: 100%;
	}
	.videoContainer:before {
	  content: '';
	  position: absolute;
	  top: 0;
	  bottom: 0;
	  left: 0;
	  right: 0;
	  display: block;
	  height: 100%;
	  z-index: 5;
	  background-image: linear-gradient(rgba(0,0,0,.0) 80%, #121122 97.5%);
	}
`;

const Video = styled('video')`
	width: 100%;
	display: block;
`;

export default function Hero() {

	const { language } = useApp();
  const text = textContext[language].home;

	const refVideo = React.useRef(null);
  React.useEffect(() => {
    refVideo.current.play();
  }, []);

	return (
		<Background id="home">
			<div className="videoContainer">
				<Video ref={refVideo} src={heroBG} id="bgVideo" playsInline muted loop poster={poster} />
			</div>
			<div className="container">
				<h1>{text.header}</h1>
				<h6>{text.body}</h6>
				{/*<AppButton large text={text.button} />*/}
			</div>
		</Background>
	)
}
