import React from 'react'
import { useApp } from '../../context/Context';
import styled from 'styled-components';
import logo from '../../assets/images/logo.webp';
import logoColor from '../../assets/images/logoColor.webp';
import { useLocation, useNavigate } from 'react-router-dom'
import textContext from '../../textContext';
import { Link } from 'react-scroll'
import Waitlist from './Waitlist';
// import languageLogo from '../../assets/images/language.webp';

// Material UI
import MenuIcon from '@mui/icons-material/Menu';
import Drawer from '@mui/material/Drawer';
// import Menu from '@mui/material/Menu';
// import MenuItem from '@mui/material/MenuItem';

const NavDrawer = styled('div')`
	width: 75vw;
	min-height: 100vh;
	position: relative;
	overflow: auto;
	@media (max-width: 400px) {
		margin: 0;
	}
`;

const Close = styled('span')`
	position: absolute;
	top: 0;
	right: 0;
	padding: 0 1rem;
	font-size: 3rem;
	cursor: pointer;
	color: white;
	font-weight: 300;
`;

const NavItems = styled('div')`
	margin-left: 2rem;
	margin-top: 2rem;
	text-transform: capitalize;
	& a {
		color: white;
		margin: auto;
		text-decoration: none;
		& p {
			margin: 0;
			padding: 1.5rem 0;
			border-bottom: 1px solid rgba(255, 255, 255, .25);
		}
	}
`;

const ImgContainer = styled('div')`
	min-width: 125px;
	.logoMain { opacity: 1; transition: .5s; }
	.logoHidden { opacity: 0; transition: .5s; }
	&:hover {
		.logoMain { opacity: 0; transition: .5s; }
		.logoHidden { opacity: 1; transition: .5s; }
	}
`;

// Nav links
const NavLinks = (props) => {
	return (
		<Link className='button-link' onClick={props.func} to={props.link} spy={true} smooth={true}>
			<p>{props.title}</p>
		</Link>
	)
};

export default function Navbar() {

	const { language, width } = useApp();
  // const [anchorEl, setAnchorEl] = React.useState(null);
  // const open = Boolean(anchorEl);
  const text = textContext[language].navbar;

  // Hamburger controls
  const [nav, setNav] = React.useState(false);

  // Drawer & navbar onclick handler
  const toggleDrawer = (event) => {
    if (event && event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }
    setNav(!nav);
  };

	let location = useLocation();
	let navigate = useNavigate();

  const handleHome = (link) => {
		if (location.pathname !== '/') {
		  navigate("/");
			nav && toggleDrawer();
			setTimeout(() => {
				let element = document.getElementById(link);
				element.scrollIntoView({behavior: "smooth"});
			}, 500)
		}
		nav && toggleDrawer();
  }

	// Handlers for dropdown
 //  const handleClick = (event) => {
 //    setAnchorEl(event.currentTarget);
 //  };
 //  const handleClose = () => {
 //    setAnchorEl(null);
 //  };

	// const handleChange = (event) => {
	// 	let val = event.target.getAttribute('value');
	// 	langSelect(val);
	// 	handleClose();
	// };

  // Navbar items
  const Items = (
  	<NavDrawer>
  		<Close onClick={toggleDrawer}>&times;</Close>
      <NavItems onClick={toggleDrawer}>
      	{
      		text.list.map((l, i) => <NavLinks func={() => handleHome(l.link)} link={l.link} key={i} title={l.title} /> )
      	}

      </NavItems>
  	</NavDrawer>
	);

	const mobileNav = (
		<nav id="mobileNav">
			<div className="container">
				<Link spy={true} smooth={true} onClick={handleHome} to="home">
					<img src={logo} alt="aperture finance" width='125' />
				</Link>
				<div style={{display:'flex',alignItems:'center'}}>
	        <Waitlist />
					<MenuIcon onClick={toggleDrawer} sx={{marginLeft:'1rem',color:'white',cursor:'pointer'}} />
				</div>
        <Drawer anchor='right' open={nav} onClose={toggleDrawer}>
          {Items}
        </Drawer>
			</div>
		</nav>
	);

  React.useEffect(() => {
  	width < 900 && document.body.classList.remove("overlay");
  }, [width])

	return (
		<React.Fragment>
		{
			width < 900 ? mobileNav : (
				<nav>
					<div id='navbar' className="container">
						<ImgContainer>
							<Link spy={true} smooth={true} style={{cursor:'pointer'}} onClick={handleHome} to="home">
								<img className="logoMain" src={logo} alt="aperture finance" width='125' />
								<img className="logoHidden" src={logoColor} alt="aperture finance" width='125' />
							</Link>
						</ImgContainer>
						<div className="linkContainer">
							{
								text.list.map((l, i) => (
									<Link key={i} to={l.link} onClick={() => handleHome(l.link)} spy={true} smooth={true}>
										<p className="linkClass">{l.title}</p>
									</Link>
								))
							}
						<div className="navButton">
							<Waitlist />
						</div>
						</div>
						{/*

						// Launch app button and Language switcher

						<div>
							<AppButton text={text.button} />
							<div
					      id="languageButton"
					      aria-controls="language-menu"
					      aria-haspopup="true"
					      aria-expanded={open ? 'true' : undefined}
					      onClick={handleClick}
					    >
					    	<img src={languageLogo} alt="language" width="16" />
					    	<p>{language}</p>
							</div>
							<Menu
								id="basic-menu"
								anchorEl={anchorEl}
								open={open}
								onClose={handleClose}
								MenuListProps={{
								'aria-labelledby': 'basic-button',
								}}
							>
								<MenuItem value='english' onClick={handleChange}>english</MenuItem>
							</Menu>
						</div>
						*/}
					</div>
				</nav>
			)
		}
		</React.Fragment>
	)
}
